data3: '5',
loadMoreVal: true,
totalPage: 1,
currentPage: 0,
status: {
pullupStatus: 'default'
},
listData:[]
}
},
components: {
"v-footer": vFooter,
"com-citychoice": comCitychoice,
search,
Scroller,
Rater,
Spinner,
Swiper,
SwiperItem,
Grid,
GridItem,
Flexbox,
FlexboxItem
},
computed: {
...mapState(["token","city","location"]),
},
